import { ExclamationCircleOutlined } from '@ant-design/icons'
import React from 'react'
import { Layout } from 'antd'
const Error = () => {
  return (
    <Layout>
      <div style={{ width: "100%",height:"50vh" ,display: "flex", justifyContent: "center", alignItems: "center", color: "red", marginTop: "100px", textShadow: "1px 1px grey" }}>
        <h3 style={{ textAlign: 'center', display: "flex", justifyContent: "center", alignItems: "center" }}><span style={{ marginRight: "10px" }}><ExclamationCircleOutlined style={{ fontSize: "20px" }} /></span>404 | Page Not Found</h3>
      </div>
    </Layout>
  )
}

export default Error