import { Image } from 'antd';
import React from 'react';
import 'react-multi-carousel/lib/styles.css';
import Noimage from "../assets/no_image.jpg";
import { createSearchParams, useNavigate } from 'react-router-dom';
const MovieCredits = ({ props }) => {
    const { id,profile_path, name, character } = props;
    const navigate=useNavigate();
    const handleClick=()=>{
        navigate({
            pathname:"/actor",
            search:createSearchParams({
                name:name,
                id:id
            }).toString()
        })
    }
    return (
        <div onClick={handleClick} style={{ cursor:"pointer",backgroundColor: "#272727", color: "#fff", margin: "0px 15px", borderRadius: "10px", padding: "10px", textAlign: "center", lineHeight: "25px" }}>
            <Image style={{ borderRadius: "10px", marginBottom: "10px" }} preview={false} src={profile_path ? `https://image.tmdb.org/t/p/original/${profile_path}` : Noimage} />
            <h3 style={{ overflow: "hidden", textOverflow: "ellipsis", height: "30px" }}>{name}</h3>
            <p style={{ overflow: "hidden", textOverflow: "ellipsis", height: "30px" }}>{character}</p>
        </div>
    )
}

export default MovieCredits