import { Layout, List, Pagination } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import MovieCard from '../components/MovieCard';

const Search = () => {
  const ref = useRef(null);
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [totalData, setTotalData] = useState(-1);
  const [searchParams, setSearchParams] = useSearchParams();
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
  const query = searchParams.get("q");
  const getPage = () => {
    const page = Number(searchParams.get("page"));
    if (page <= 0) {
      return 1;
    } else if (page > (Math.ceil(totalData / 20))) {
      return 1;
    } else {
      return page;
    }
  }
  const [currentPage, setCurrentPage] = useState(1);
  const fetchQuery = async (query, currentPage) => {
    setLoading(true);
    const res = await fetch(`https://api.themoviedb.org/3/search/multi?api_key=${API_KEY}&language=en-US&query=${query}&page=${currentPage}&include_adult=false`);
    try {
      const jsonData = await res.json();
      setMovies(jsonData.results);
      if (jsonData.total_results === 0) {
        setTotalData(0);
      } else {
        setTotalData(jsonData.total_results);
      }
    } catch (error) {
      console.log(error)
    } finally {
      setLoading(false);
    }
  }
  const page = getPage();
  const updatePage = (value) => {
    searchParams.set("page", value);
    setSearchParams(searchParams);
    setCurrentPage(value);
    ref.current.scrollIntoView({ behavior: "smooth" });
  }
  useEffect(() => {
    setCurrentPage(page);
    fetchQuery(query, currentPage)
  }, [query, currentPage, page])
  if (totalData === 0) {
    return <p className='movieTitle' style={{ textAlign: "center", lineHeight: "25px", margin: "25px" }}>
      <span>Oops! No Results were Found!</span>
      <br />
      <span>Check Your Spelling If Incorrect.</span>
      <br />
      <span>Search The Correct Word To Get Desired Results!</span>
      <br />
      <span>Use Google To Search The Movie Correct Name.</span>
      <br />
      <span>Search The Exact Name To Get Better Filter Results!</span>
    </p>
  }
  return (
    <>
      <Layout>
        <h2 ref={ref} style={loading ? { display: "none" } : { textAlign: "left", padding: "15px" }}>Search Results For : <span style={{ color: "red" }}>{query}</span></h2>
        <List
          loading={loading}
          grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
          dataSource={movies}
          renderItem={(elem) => {
            return <MovieCard key={elem.id} props={elem} />;
          }}
        />
        <Pagination
          style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
          responsive={true}
          current={currentPage}
          onChange={updatePage}
          pageSize={20}
          total={totalData}
          showSizeChanger={false}
        />
      </Layout>
    </>
  )
}

export default Search