import { Col, Image, Layout, Row } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import BgImage from "../assets/bg.jpg";
import Noimage from "../assets/no_image.jpg";
import ActorEpisodes from './ActorEpisodes';
import ActorMovies from './ActorMovies';
const Actors = () => {
    const [loading, setLoading] = useState(true);
    const [actorData, setActorData] = useState({});
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
    const [searchParams] = useSearchParams();
    const ref = useRef(null);
    const personId = searchParams.get("id");

    const getDetails = async () => {
        const res = await fetch(`https://api.themoviedb.org/3/person/${personId}?api_key=${API_KEY}&language=en-US`);
        try {
            const data = await res.json();
            setActorData(data);
        } catch (error) {
            console.log(error)
        } finally {
            setLoading(false);
        }
    }

    useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
        getDetails(personId);
    }, [personId])

    const { name, birthday, place_of_birth, known_for_department, biography, profile_path } = actorData;
    return (
        <>
            <Layout>
                <div style={loading ? { display: "none" } : { backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url(${BgImage})`, backgroundSize: "cover", padding: "20px", width: "100%" }}>
                    <Row gutter={[16, 16]}>
                        <Col md={12}>
                            <Image preview src={profile_path ? `https://image.tmdb.org/t/p/original/${profile_path}` : Noimage} />
                        </Col>
                        <Col md={12}>
                            <h2 className='movieTitle'>{name}</h2>
                            <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>DOB :</span> {birthday}</p>
                            <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Birth Place :</span> {place_of_birth}</p>
                            <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Department :</span> {known_for_department}</p>
                            <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Biography :</span> {biography}</p>
                        </Col>
                    </Row>
                </div>
                <ActorMovies />
                <ActorEpisodes />
            </Layout>
        </>
    )
}

export default Actors