import React, { useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom';
import { List, Pagination } from "antd";
import MovieCard from './MovieCard';
const Recommended = () => {
    const ref=useRef(null);
    const { type, id } = useParams();
    const [data, setData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const [totalData, setTotalData] = useState(0);
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;

    const fetchData = async (type, id, currentPage) => {
        const res = await fetch(`https://api.themoviedb.org/3/${type}/${id}/similar?api_key=${API_KEY}&language=en-US&page=${currentPage}`);
        try {
            const jsonData = await res.json();
            setData(jsonData.results);
            setTotalData(jsonData.total_results);
        } catch (error) {
            console.log(error);
        } finally {
            setLoading(false);
        }
    }
    useEffect(() => {
        fetchData(type, id, currentPage);
    }, [type, id, currentPage])

    const updatePage = (page) => {
        setCurrentPage(page);
        ref.current.scrollIntoView({behaviour:"smooth"});
    }
    return (
        <>
            <h2 ref={ref} style={loading ? { display: "none" } : { backgroundColor: "wheat", textAlign: "center", padding: "10px", color: "teal" }}>You Might Also Like</h2>
            <List
                grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
                loading={loading}
                dataSource={data}
                renderItem={(elem) => {
                    return <MovieCard key={elem.id} props={elem} type={type} />
                }}
            />
            <Pagination
                style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
                responsive={true}
                current={currentPage}
                onChange={updatePage}
                pageSize={data.length}
                total={totalData}
                showSizeChanger={false}
            />
        </>
    )
}

export default Recommended