import { Layout } from 'antd';
import React from 'react';
const {Footer } = Layout;
const DearTvFooter = () => {
  return (
    <Footer
        style={{
          textAlign: 'center'
        }}
      >
        DearTV 2023 Created by Vivek Mishra
      </Footer>
  )
}

export default DearTvFooter