import React, { useEffect, useState } from 'react';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import MovieCard from './MovieCard';
const TrendingMovies = () => {
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(true);
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
    const getMovies = async () => {
        const res = await fetch(`https://api.themoviedb.org/3/trending/movie/day?api_key=${API_KEY}&language=en-US&page=1`);
        try {
            const data = await res.json();
            setMovies(data.results);
            setLoading(false);
        } catch (error) {
            console.log(error)
        }
    }
    useEffect(() => {
        getMovies();
    }, [])
    const responsive = {
        superLargeDesktop: {
            breakpoint: { max: 4000, min: 3000 },
            items: 6
        },
        desktop: {
            breakpoint: { max: 3000, min: 1024 },
            items: 5
        },
        tablet: {
            breakpoint: { max: 1024, min: 464 },
            items: 2
        },
        mobile: {
            breakpoint: { max: 464, min: 0 },
            items: 1
        }
    };
    return (
        <>
            <h2 style={loading?{display:"none"}:{ backgroundColor: "wheat", textAlign: "center", padding: "10px" }}>Today's Trending Movies</h2>
            <Carousel responsive={responsive}>
                {movies.map((elem) => (
                    <MovieCard key={elem.id} props={elem} />
                ))}
            </Carousel>
        </>
    )
}

export default TrendingMovies