import { Carousel } from "antd";
import React, { useEffect, useState } from "react";
const CarouselSlide = () => {
  const [carouselData, setCarouselData] = useState([]);
  const [loading, setLoading] = useState(true);
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
  const getCarouselData = async () => {
    const res = await fetch(
      `https://api.themoviedb.org/3/trending/movie/day?api_key=${API_KEY}&page=1`
    );
    try {
      const data = await res.json();
      setCarouselData(data.results);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(() => {
    getCarouselData();
  }, [])
  return (
    <Carousel style={loading ? { display: "none" } : { display: "block" }} autoplay={true} easing="true">
      {carouselData.slice(0, 10).map((elem) => (
        <div key={elem.id}>
          <img
            className="img-carousel"
            width={"100%"}
            src={`https://image.tmdb.org/t/p/original/${elem.backdrop_path}`}
            alt={elem.title}
          />
          <div style={{backgroundColor: "transparent",position: "absolute", bottom: "10px",fontSize:"18px",overflow:"hidden" }}>
            <p style={loading ? {display:"none"}:{}} className="carousel-text">{elem.title.length>30?elem.title.slice(0,30)+"..":elem.title}</p>
          </div>
        </div>
      ))}
    </Carousel>
  );
};

export default CarouselSlide;
