import { Col, Image, Rate, Row } from 'antd';
import React from 'react'
import Noimage from "../assets/no_image.jpg";
const MovieDetailsCard = ({ props, type }) => {
    let poster = props.poster_path;
    if (poster != null) {
        poster = `https://image.tmdb.org/t/p/original/${props.poster_path}`;
    } else {
        poster = Noimage
    }
    if (type == "movie") {
        const { budget, overview, revenue, runtime, status, title, vote_average, genres, release_date } = props;
        let genre = "";
        for (const g in genres) {
            genre += genres[g].name + ", ";
        }
        return (
            <Row gutter={[16, 16]}>
                <Col md={12}>
                    <Image src={poster} preview />
                </Col>
                <Col md={12}>
                    <h1 className='movieTitle'>{title}</h1>
                    <Rate allowHalf disabled defaultValue={vote_average / 2} />
                    <p className='dataText' ><span style={{ color: "red", textShadow: "1px 1px black" }}>Duration : </span>{runtime !=undefined || runtime!=null ? Math.floor(runtime / 60) + " h " + runtime % 60 + " min":0}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Status : </span>{status}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Description : </span>{overview}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Budget : </span>{budget != undefined?budget.toLocaleString():0}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Revenue : </span>{revenue != undefined?revenue.toLocaleString():0}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Release Date : </span>{release_date!=undefined||release_date!=null?new Date(release_date).toDateString():"N/A"}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Genres : </span>{genre}</p>
                </Col>
            </Row>
        )

    } else {
        const { overview, episode_run_time, status, name, vote_average, genres, number_of_episodes, number_of_seasons, first_air_date } = props;
        let genre = "";
        for (const g in genres) {
            genre += genres[g].name + ", ";
        }
        console.log(episode_run_time)
        return (
            <Row gutter={[16, 16]}>
                <Col md={12}>
                    <Image src={poster} preview />
                </Col>
                <Col md={12}>
                    <h1 className='movieTitle'>{name}</h1>
                    <Rate allowHalf disabled defaultValue={vote_average / 2} />
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Duration : </span>{episode_run_time!=undefined||episode_run_time!=null?Math.floor(episode_run_time / 60) + " h : " + episode_run_time % 60 + " min":0}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Status : </span>{status}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Total Episodes : </span>{number_of_episodes}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Total Seasons : </span>{number_of_seasons}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Description : </span>{overview}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Release Date : </span>{first_air_date!=undefined||first_air_date!=null?new Date(first_air_date).toDateString():"N/A"}</p>
                    <p className='dataText'><span style={{ color: "red", textShadow: "1px 1px black" }}>Genres : </span>{genre}</p>
                </Col>
            </Row>
        )
    }
}

export default MovieDetailsCard