import {
  CloseOutlined,
  HomeOutlined,
  MenuOutlined,
  PictureOutlined,
  SearchOutlined,
  TeamOutlined
} from "@ant-design/icons";
import { Drawer, Dropdown, Menu } from "antd";
import { useRef, useState } from "react";
import { Link, createSearchParams, useLocation, useNavigate } from "react-router-dom";
import Logo from "../assets/logo-color.png";
const Navbar = () => {
  const ref=useRef();
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const [showSearch, setShowSearch] = useState(false);
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
  const [suggestion, setSuggestion] = useState([]);
  const fetchQuery = async (query) => {
    const res = await fetch(`https://api.themoviedb.org/3/search/multi?api_key=${API_KEY}&language=en-US&query=${query}&page=1&include_adult=false`);
    try {
      const jsonData = await res.json();
      setSuggestion(jsonData.results);
    } catch (error) {
      console.log(error)
    }
  }

  const openDrawer = () => {
    setOpen(true);
  };
  const closeDrawer = () => {
    setOpen(false);
  };
  const handleNavigate = (e) => {
    navigate(e.key);
    setOpen(false);
  }
  const handleSearchQuery = (e) => {
    fetchQuery(e.target.value);
    if (e.keyCode === 13) {
      const query = e.target.value.toLowerCase();
      navigate({
        pathname: "/searchresult",
        search: createSearchParams({
          q: query,
          page: 1
        }).toString()
      });
      setShowSearch(false);
      e.target.value = "";
    }
  }
  const suggestionSelect = (e) => {
    const query=e.target.innerText;
    navigate({
      pathname: "/searchresult",
      search: createSearchParams({
        q: query,
        page: 1
      }).toString()
    });
    setShowSearch(false);
    e.target.value = "";
    if(ref.current.value===""){
      e.target.value="";
    }
  }
  const handleShowSearch = () => {
    setShowSearch(true);
  }
  const closeSearch = () => {
    setShowSearch(false);
  }
  const items = [
    {
      key: "28",
      label: "Action"
    },
    {
      key: "12",
      label: "Adventure"
    },
    {
      key: "16",
      label: "Anime"
    },
    {
      key: "35",
      label: "Comedy"
    },
    {
      key: "80",
      label: "Crime"
    },
    {
      key: "99",
      label: "Documentary"
    },
    {
      key: "18",
      label: "Drama"
    },
    {
      key: "10751",
      label: "Family"
    },
    {
      key: "14",
      label: "Fantasy"
    },
    {
      key: "27",
      label: "Horror"
    },
    {
      key: "9648",
      label: "Mystery"
    },
    {
      key: "10749",
      label: "Romance"
    },
    {
      key: "878",
      label: "Sci_Fi"
    },
    {
      key: "10770",
      label: "TV"
    },
    {
      key: "53",
      label: "Thriller"
    },
    {
      key: "10752",
      label: "War"
    }
  ];
  const handleGenre = (e) => {
    const genreId = e.key;
    const genre = items.find((elem) => elem.key === genreId).label;
    navigate({
      pathname: "/genre",
      search: createSearchParams({
        query: genre,
        genreId: genreId,
        page: 1
      }).toString()
    })
    setOpen(false);
  }
  const AppMenu = () => {
    return (
      <>
        <Link to={"/"}>
          <div style={{ marginLeft: "15px" }} className="headerMenu">
            <img width={120} src={Logo} alt="logo" />
          </div>
        </Link>
        <Menu
          selectedKeys={pathname}
          className="headerMenu"
          mode="horizontal"
          style={{ backgroundColor: "#471AA0", color: "#fff", justifyContent: "right", alignItems: "center", textAlign: "center", width: "80%" }}
          items={
            [
              {
                label: "Search",
                key: "search",
                icon: <SearchOutlined />,
                onClick: handleShowSearch
              },
              {
                label: "Home",
                key: "/",
                icon: <HomeOutlined />,
                onClick: handleNavigate
              },
              {
                label: "About Us",
                key: "/about",
                icon: <TeamOutlined />,
                onClick: handleNavigate
              },
              {
                icon: <PictureOutlined />,
                label: <Dropdown arrow trigger={"click"} menu={{ onClick: handleGenre, items: items }} ><span>Genres</span></Dropdown>
              }
            ]
          }
        >
        </Menu>
      </>

    );
  };

  return (

    <>
      <nav style={{
        top: "0", zIndex: "50", position: "sticky",
        display: "flex", justifyContent: "space-between",
        alignItems: "center", backgroundColor: "#471AA0"
      }}>
        <AppMenu />
        <div className="overlay" style={showSearch ? { display: "block" } : { display: "none" }}>
          <span className="closebtn" onClick={closeSearch} title="Close Search"><CloseOutlined style={{ fontSize: "30px" }} /></span>
          <div className="overlay-content">
            <input ref={ref} onKeyUp={handleSearchQuery} className="search-input" type="text" placeholder="Search for movies.." />
            <div className="search-suggestion">
              {suggestion.map((elem) => (
                elem.title && <p className="suggestedText" key={elem.id} style={{cursor:"pointer"}} onClick={suggestionSelect}>{elem.title}</p>
              ))}
              {suggestion.map((elem) => (
                elem.name && <p className="suggestedText" key={elem.id} style={{cursor:"pointer"}} onClick={suggestionSelect}>{elem.name}</p>
              ))}
            </div>
          </div>
        </div>
        <div className="drawer-menu" style={{ width: "100%" }}>
          <div style={{ width: "100%", display: "flex", justifyContent: "space-between", alignItems: "center", padding: "0 15px" }}>
            <div onClick={openDrawer}>
              <MenuOutlined style={{ fontSize: "24px", color: "#fff" }} />
            </div>
            <Link to={"/"}>
              <div>
                <img width={100} src={Logo} alt="logo" />
              </div>
            </Link>
            <div onClick={handleShowSearch}>
              <SearchOutlined style={{ fontSize: "24px", color: "#fff" }} />
            </div>
          </div>
          <Drawer
            title={<Link to={"/"}><img width={100} src={Logo} alt="logo" /></Link>}
            placement="left"
            closeIcon={<CloseOutlined style={{ color: "#fff" }} />}
            style={{ backgroundColor: "#471AA0"}}
            width={"60%"}
            open={open}
            onClose={closeDrawer}
          >
            <Menu
              mode="inline"
              style={{backgroundColor: "#471AA0", color: "#fff", display: "inline", justifyContent: "center", alignItems: "center" }}
              items={
                [
                  {
                    label: "Home",
                    key: "/",
                    icon: <HomeOutlined />,
                    onClick: handleNavigate
                  },
                  {
                    label: "About Us",
                    key: "/about",
                    icon: <TeamOutlined />,
                    onClick: handleNavigate
                  },
                  {
                    icon: <PictureOutlined />,
                    label: <Dropdown arrow trigger={"click"} menu={{ onClick: handleGenre, items: items }} ><span style={{ width: "100%" }}>Genres</span></Dropdown>
                  }
                ]
              }
            />
          </Drawer>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
