import { List, Pagination } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import MovieCard from '../components/MovieCard';
import {Layout} from "antd"
const Genres = () => {
    const [searchParams, setSearchParams] = useSearchParams();
    const ref = useRef(null);
    const [data, setData] = useState([]);
    const [totalData, setTotalData] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
    const genreId = searchParams.get("genreId");
    const genre = searchParams.get("query");
    const fetchMovie = async (genreId, currentPage) => {
        const res = await fetch(`https://api.themoviedb.org/3/discover/movie?api_key=${API_KEY}&with_genres=${genreId}&page=${currentPage}`);
        try {
            const jsonData = await res.json();
            setData(jsonData.results);
            setTotalData(jsonData.total_results);
        } catch (error) {
            console.log(error);
        } finally {
            setLoading(false);
        }
    }
    const getPage = () => {
        const page = Number(searchParams.get("page"));
        if (page <= 0) {
            return 1;
        } else if (page > (Math.ceil(totalData / 20))) {
            return 1;
        } else {
            return page;
        }
    }
    const page = getPage();
    useEffect(() => {
        window.scrollTo({ top: "0", behavior: "smooth" });
        fetchMovie(genreId, currentPage);
        setCurrentPage(page);
    }, [genreId, currentPage, page])

    const updatePage = (value) => {
        setCurrentPage(value);
        searchParams.set("page", value);
        setSearchParams(searchParams);
        ref.current.scrollIntoView({ behavior: "smooth" });
    }

    return (
        <>
            <Layout>
                <h2 ref={ref} style={loading ? { display: "none" } : { textAlign: "left", padding: "15px"}}>Genre : <span style={{ color: "red" }}>{genre}</span></h2>
                <List
                    loading={loading}
                    grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
                    dataSource={data}
                    renderItem={(elem) => {
                        return <MovieCard key={elem.id} props={elem} type={"movie"} />;
                    }}
                />
                <Pagination
                    style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
                    responsive={true}
                    current={currentPage}
                    onChange={updatePage}
                    pageSize={20}
                    total={totalData}
                    showSizeChanger={false}
                />
            </Layout>
        </>
    )
}

export default Genres