import { List, Pagination } from 'antd';
import React, { useEffect, useRef, useState } from 'react'
import { useSearchParams } from 'react-router-dom';
import MovieCard from './MovieCard';
const ActorMovies = () => {
    const [currentPage, setCurrentPage] = useState(1);
    const [itemPerPage, setItemPerPage] = useState(20);
    const [newMovieData, setNewMovieData] = useState([]);
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(true);
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
    const [searchParams] = useSearchParams();
    const ref = useRef(null);
    const personId = searchParams.get("id");
    const getMovies = async (personId) => {
        const res = await fetch(`https://api.themoviedb.org/3/person/${personId}/movie_credits?api_key=${API_KEY}&language=en-US`);
        try {
            const data = await res.json();
            setMovies(data.cast);
        } catch (error) {
            console.log(error)
        } finally {
            setTimeout(() => {
                setLoading(false);
            }, 2000)
        }
    }
    useEffect(() => {
        getMovies(personId);
    }, [personId])
    const pageHandler = (e) => {
        setCurrentPage(e);
        ref.current.scrollIntoView({ behavior: "smooth" });
    }
    const onShowSizeChange = (current, pageSize) => {
        setCurrentPage(current);
        setItemPerPage(pageSize);
    };
    const displayMovieData = (currentPage, itemPerPage, data) => {
        const startIndex = (currentPage - 1) * itemPerPage;
        const endIndex = startIndex + itemPerPage;
        const pageData = data.slice(startIndex, endIndex);
        setNewMovieData(pageData);
    }
    useEffect(() => {
        displayMovieData(currentPage, itemPerPage, movies);
    }, [currentPage, itemPerPage, movies])
    return (
        <>
            <h2 ref={ref} style={loading ? { display: "none" } : { backgroundColor: "wheat", textAlign: "center", padding: "10px", color: "teal" }}>Movies</h2>
            <List
                loading={loading}
                grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
                dataSource={newMovieData}
                renderItem={(elem) => {
                    return <MovieCard key={elem.id} props={elem} type={"movie"} />;
                }}
            />
            <Pagination
                style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
                responsive={true}
                total={movies.length}
                current={currentPage}
                pageSize={itemPerPage}
                onChange={pageHandler}
                onShowSizeChange={onShowSizeChange}
            />
        </>
    )
}

export default ActorMovies