import { List, Pagination } from "antd";
import React, { useEffect, useRef, useState } from "react";
import MovieCard from "./MovieCard";
const WeeklyPopularMovies = () => {
  const ref=useRef(null);
  const [movies, setMovies] = useState([]);
  const [totalData, setTotalData] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;

  const updatePage = (e) => {
    setCurrentPage(e);
    ref.current.scrollIntoView({behavior:"smooth"});
  }

  const getPopularMoviesWeekly = async (currentPage) => {
    setLoading(true);
    const res = await fetch(
      `https://api.themoviedb.org/3/trending/movie/week?api_key=${API_KEY}&page=${currentPage}`
    );
    try {
      const data = await res.json();
      setTotalData(data.total_results);
      setMovies(data.results);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getPopularMoviesWeekly(currentPage);
  }, [currentPage]);

  return (
    <>
      <h2 ref={ref} style={loading ? { display: "none" } : { backgroundColor: "wheat", textAlign: "center", padding: "10px" }}>Weekly Trending Movies</h2>
      <List
        loading={loading}
        grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
        dataSource={movies}
        renderItem={(elem) => {
          return <MovieCard key={elem.id} props={elem} />;
        }}
      />
      <Pagination
        style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
        responsive={true}
        current={currentPage}
        onChange={updatePage}
        pageSize={movies.length}
        total={totalData}
        showSizeChanger={false}
      />
    </>
  );
};

export default WeeklyPopularMovies;
