import { Card, Image, Rate, Tooltip } from "antd";
import React from "react";
import { Link } from "react-router-dom";
import Noimage from "../assets/no_image.jpg"
const MovieCard = ({ props, type }) => {

  let poster = "";
  let rating = 0;
  let releaseDate = "";
  let movieName = "";
  if (props.media_type == "movie" || type == "movie") {
    let { poster_path, release_date, title, vote_average } = props;
    if (poster_path != null) {
      poster = `https://image.tmdb.org/t/p/original/${poster_path}`;
    } else {
      poster = Noimage
    }
    rating = vote_average / 2;
    releaseDate = release_date;
    movieName = title;
  }
  if (props.media_type == "tv" || type == "tv") {
    let { first_air_date, name, poster_path, vote_average } = props;
    if (poster_path != null) {
      poster = `https://image.tmdb.org/t/p/original/${poster_path}`;
    } else {
      poster = Noimage
    }
    rating = vote_average / 2;
    releaseDate = first_air_date;
    movieName = name;
  }
  const { Meta } = Card;

  return (
    <Link to={type == undefined ? `/stream/${props.media_type}/${props.id}` : `/stream/${type}/${props.id}`}>
      <Tooltip title={`Watch Now`}>
        <Card
          hoverable
          style={props.media_type == "tv" || props.media_type == "movie" || type == "movie" || type == "tv" ? { margin: "15px" } : { display: "none"}}
          cover={
            <Image
              preview={false}
              src={poster}
              alt={"jpeg"}
            />
          }
        >
          <div style={{ lineHeight: "30px" }}>
            <Meta avatar={<Rate disabled allowHalf defaultValue={rating} />} />
            <Meta description={new Date(releaseDate).toDateString()} />
            <Meta title={movieName} />
          </div>
        </Card>
      </Tooltip>
    </Link>
  );
};

export default MovieCard;
