import { List, Pagination } from 'antd';
import React, { useEffect, useRef, useState } from 'react'
import { useSearchParams } from 'react-router-dom';
import MovieCard from './MovieCard';

const ActorEpisodes = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const [itemPerPage, setItemPerPage] = useState(20);
  const [loading, setLoading] = useState(true);
  const [tv, setTv] = useState([]);
  const [newTvData, setNewTvData] = useState([]);
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
  const [searchParams] = useSearchParams();
  const ref = useRef(null);
  const personId = searchParams.get("id");
  const getTv = async (personId) => {
    const res = await fetch(`https://api.themoviedb.org/3/person/${personId}/tv_credits?api_key=${API_KEY}&language=en-US`);
    try {
      const data = await res.json();
      setTv(data.cast);
    } catch (error) {
      console.log(error)
    }finally{
      setTimeout(()=>{
        setLoading(false);
      },2000)
    }
  }
  useEffect(() => {
    getTv(personId);
  }, [personId])
  const pageHandler = (e) => {
    setCurrentPage(e);
    ref.current.scrollIntoView({ behavior: "smooth" });
  }
  const onShowSizeChange = (current, pageSize) => {
    setCurrentPage(current);
    setItemPerPage(pageSize);
  };
  const displayMovieData = (currentPage, itemPerPage, data) => {
    const startIndex = (currentPage - 1) * itemPerPage;
    const endIndex = startIndex + itemPerPage;
    const pageData = data.slice(startIndex, endIndex);
    setNewTvData(pageData);
  }
  useEffect(() => {
    displayMovieData(currentPage, itemPerPage, tv);
  }, [currentPage, itemPerPage, tv])

  return (
    <>
      <h2 ref={ref} style={loading ? { display: "none" } : { backgroundColor: "wheat", textAlign: "center", padding: "10px", color: "teal", marginTop: "100px" }}>TV Episodes</h2>
      <List
        grid={{ xs: 1, sm: 2, md: 3, lg: 4, xl: 5, xxl: 6 }}
        dataSource={newTvData}
        renderItem={(elem) => {
          return <MovieCard key={elem.id} props={elem} type={"tv"} />;
        }}
      />
      <Pagination
        style={loading ? { display: "none" } : { float: "left", backgroundColor: "whitesmoke", fontWeight: "bolder", padding: "10px", borderRadius: "10px", margin: "15px" }}
        responsive={true}
        total={tv.length}
        current={currentPage}
        pageSize={itemPerPage}
        onChange={pageHandler}
        onShowSizeChange={onShowSizeChange}
      />
    </>
  )
}

export default ActorEpisodes