import { TreeSelect } from 'antd';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

const MoviePlayer = () => {
    const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
    const [loading, setLoading] = useState(true);
    const { type, id } = useParams();
    const [value, setValue] = useState("1-1");
    const [data, setData] = useState([]);
    const onChange = (newValue) => {
        if(newValue.length>1){
            setValue(newValue);
        }
    };
    const fetchMovie = async (id, type) => {
        const res = await fetch(`https://api.themoviedb.org/3/${type}/${id}?api_key=${API_KEY}`);
        try {
            const json = await res.json();
            setData(json.seasons);
        } catch (error) {
            console.log(error)
        } finally {
            setLoading(false);
        }
    }
    useEffect(() => {
        if (type === "tv") {
            fetchMovie(id, type);
        }
    }, [id, type])
    const treeData = [];

    if (type === "tv") {
        data.map((elem) => {
            if (elem.season_number > 0) {
                let t = `Season ${elem.season_number}`;
                let v = elem.season_number;
                let season = {
                    title: t,
                    value: v,
                    children: [],
                }
                for (let i = 1; i <= elem.episode_count; i++) {
                    let child = {
                        title: `${t} - Episode ${i}`,
                        value: `${v}-${i}`
                    }
                    season.children.push(child);
                }
                treeData.push(season);
            }
        })
    }
    const tvEpisode =value.split("-");
    let source = "";
    if (type === "tv") {
        source = `https://autoembed.to/tv/tmdb/${id}-${tvEpisode[0]}-${tvEpisode[1]}?server=2`;
    } else {
        source = `https://autoembed.to/movie/tmdb/${id}?server=2`;
    }
    return (
        <>
            <div style={type === "tv" ? { margin: "auto", width: "50%", marginBottom: "20px", marginTop: "50px" } : { display: "none" }}>
                <TreeSelect
                    style={loading ? { display: "none" } : {
                        width: '100%',
                    }}
                    value={value}
                    dropdownStyle={{
                        maxHeight: 400,
                        overflow: 'auto',
                    }}
                    treeData={treeData}
                    treeIcon
                    onChange={onChange}
                />
            </div>
            <div className="videoPlayer">
                <iframe
                    id="iframe"
                    width="100%"
                    height="100%"
                    frameborder="0"
                    allowFullScreen={true}
                    src={source}
                />
            </div>
        </>
    )
}

export default MoviePlayer