import { Spin } from 'antd';
import React, { useEffect, useState } from 'react';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import { useParams } from 'react-router-dom';
import MovieCredits from './MovieCredits';
import MovieDetailsCard from './MovieDetailsCard';

const MovieDeatils = () => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState({});
  const [credits, setCredits] = useState([]);
  const API_KEY = process.env.REACT_APP_TMDB_API_KEY;
  const { type, id } = useParams();
  const mediaType = type == "movie" ? "movie" : "tv";
  const fetchMovie = async (id) => {
    const res = await fetch(`https://api.themoviedb.org/3/${mediaType}/${id}?api_key=${API_KEY}`);
    try {
      const json = await res.json();
      setData(json);
    } catch (error) {
      console.log(error)
    } finally {
      setLoading(false);
    }
  }
  const fetchCredits = async () => {
    setLoading(true);
    const res = await fetch(`https://api.themoviedb.org/3/${mediaType}/${id}/credits?api_key=${API_KEY}`);
    try {
      const json = await res.json();
      setCredits(json.cast);
    } catch (error) {
      console.log(error)
    } finally {
      setLoading(false);
    }
  }
  useEffect(() => {
    fetchMovie(id);
    fetchCredits(id);
  }, [id])
  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 7
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 6
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 3
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };
  if (loading) {
    return (
      <Spin style={{ width: "100%", margin: "auto", marginTop: "100px" }} tip="Loading.." size="large" />
    )
  } else {
    return (
      <>
        <div style={{ backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url(https://image.tmdb.org/t/p/original/${data.backdrop_path})`, backgroundSize: "cover", padding: "20px" }}>
          <MovieDetailsCard props={data} type={type} />
        </div>
        <h1 style={{ textAlign: "center", margin: "50px"}}>CASTS</h1>
        <Carousel responsive={responsive}>
          {credits.map((elem) => (
            <MovieCredits key={elem.id} props={elem} />
          ))}
        </Carousel>
      </>
    )
  }
}

export default MovieDeatils